body {
  margin: 0px;
  padding: 0px;
  background: #f7f5f5;
}

header {
  height: 30px;
  padding: 20px;
  background: blue;
  color: white;
  font-weight: 600;
  font-size: 24px;
}

footer {
  position: absolute;
  bottom: 0;
  text-align: center;
  padding: 10px;
  width: 100vw;
}
.contents {
  display: flex;
  justify-content: space-evenly;
  padding: 10px;
  padding-top: 5%;
}
.box {
  padding: 15px;
  width: 15%;
  background: white;
  border-radius: 16px;
}
